export default {
	data() {
		return {
			data: [], //病毒知识数据
			recommendListr: [], //防护列表数据
			loading: false
		};
	},
	created() {
		this.getData();
	},
	methods: {
		getData() {
			uni.request({
				method: 'GET',
				url: 'https://ncov.zhouxuanyu.com/WikiList',
				success: res => {
					this.data = res.data.result.slice(1);
				}
			});
			// 获取防护知识数据
			uni.request({
				method: 'GET',
				url: 'https://ncov.zhouxuanyu.com/fanghu',
				success: res => {
					setTimeout(() => {
						this.loading = true;
					}, 500);
					this.recommendListr = res.data;
				}
			});
		}
	}
};
